import React, { useEffect } from "react";
import { makeStyles } from "@material-ui/core/styles";
import Card from "@material-ui/core/Card";
import CardActionArea from "@material-ui/core/CardActionArea";
import Typography from "@material-ui/core/Typography";
import MicIcon from "@material-ui/icons/Mic";
import DescriptionIcon from "@material-ui/icons/Description";
import { useDispatch, useSelector } from "react-redux";
import Hidden from "@material-ui/core/Hidden";

import { fetchPublicFeed } from "../../redux/actions";

const useStyles = makeStyles((theme) => ({
    root: {
        marginRight: 120,
        marginLeft: 120,
        margin: 60,
        [theme.breakpoints.down("sm")]: {
            marginRight: 60,
            marginLeft: 60,
        },
        [theme.breakpoints.down("xs")]: {
            marginRight: 20,
            marginLeft: 20,
        },
    },
    heading: {
        fontFamily: "IBM Plex Sans",
        textTransform: "uppercase",
        color: "#0066FF",
        fontWeight: 800,
        textAlign: "left",
        fontSize: 24,
        paddingLeft: 16,
        borderLeft: `4px solid #0066FF`,
        [theme.breakpoints.down("sm")]: {
            fontSize: 16,
        },
    },
    allPodsContainer: {
        display: "flex",
        flexDirection: "row",
        flexWrap: "wrap",
    },
    cardContainer: {
        width: "60%",
        height: 160,
        margin: 16,
        marginLeft: 0,
        marginBottom: 0,
        [theme.breakpoints.down("sm")]: {
            marginRight: 0,
            width: "100%",
        },
        [theme.breakpoints.down("xs")]: {
            height: 88,
        },
    },
    actionArea: {
        display: "flex",
        flexDirection: "row",
        justifyContent: "space-between",
        padding: 8,
    },
    media: {
        alignSelf: "flex-start",
        width: 144,
        height: 144,
        borderRadius: 4,
        [theme.breakpoints.down("xs")]: {
            width: 72,
            height: 72,
        },
    },
    podText: {
        alignSelf: "flex-start",
        display: "flex",
        height: 144,
        marginRight: 8,
        flexDirection: "column",
        justifyContent: "space-between",
        [theme.breakpoints.down("xs")]: {
            height: 72,
        },
    },
    podTitle: {
        fontFamily: "IBM Plex Sans",
        fontWeight: 600,
        fontSize: 16,
        lineHeight: "20px",
        height: 40,
        overflow: "hidden",
    },
    podSubTitle: {
        fontFamily: "IBM Plex Sans",
        fontWeight: 400,
        fontSize: 16,
        lineHeight: "20px",
        height: 40,
        overflow: "hidden",
    },
}));

const PodsCard = (props) => {
    const classes = useStyles();
    const { author, narrator, title, description, image } = props;

    return (
        <Card className={classes.cardContainer}>
            <CardActionArea className={classes.actionArea}>
                <div className={classes.podText}>
                    <div>
                        <div className={classes.podTitle}>{title}</div>
                        <Hidden only="xs">
                            <div className={classes.podSubTitle}>
                                {description}
                            </div>
                        </Hidden>
                    </div>
                    <div
                        style={{
                            display: "flex",
                            flexDirection: "row",
                        }}>
                        <DescriptionIcon
                            style={{
                                margin: 0,
                                color: "#888888",
                                fontSize: 16,
                            }}
                        />
                        <Typography
                            variant="h6"
                            color="textSecondary"
                            style={{ fontSize: 12 }}
                            component="h6">
                            {author}
                        </Typography>
                        <MicIcon
                            style={{
                                margin: 0,
                                marginLeft: 8,
                                color: "#888888",
                                fontSize: 16,
                            }}
                        />
                        <Typography
                            variant="h6"
                            color="textSecondary"
                            style={{ fontSize: 12 }}
                            component="h6">
                            {narrator}
                        </Typography>
                    </div>
                </div>
                <img className={classes.media} src={image} alt={title} />
            </CardActionArea>
        </Card>
    );
};

const MightLike = () => {
    const classes = useStyles();

    const { list } = useSelector((state) => state.publicFeed);
    const dispatch = useDispatch();

    useEffect(() => {
        dispatch(fetchPublicFeed());
    }, [dispatch]);

    return (
        <section className={classes.root}>
            <h2 className={classes.heading}>You Might Also Like</h2>
            <div className={classes.trendingPods}>
                {list.map((item) => (
                    <PodsCard
                        key={item.id}
                        title={item.title}
                        description={item.description}
                        author={`${`${item.author.firstName} ${item.author.lastName}`}`}
                        narrator={`${`${item.narrator.firstName} ${item.narrator.lastName}`}`}
                        image={item.imageURLs[0]}
                    />
                ))}
            </div>
        </section>
    );
};

export default MightLike;
